import React, { useState, useRef } from 'react';
import '../styles/InputArea.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUpload, faArrowUp } from '@fortawesome/free-solid-svg-icons';
import { uploadFile, askQuestion, fetchSentences, checkProgress as apiCheckProgress, getResults as apiGetResults } from '../services/api';

const InputArea = ({
  currentFile,
  setCurrentFile,
  setUploadStatus,
  isProcessing,
  setIsProcessing,
  setCurrentQuestion,
  setCurrentQuestionId,
  setProvenanceSources,
  setAnswer
}) => {
  const [questionText, setQuestionText] = useState('');
  const fileInputRef = useRef(null);
  const [pollingInterval, setPollingInterval] = useState(null);

  const handleFileUpload = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const formData = new FormData();
    formData.append('file', file);

    setUploadStatus({
      success: false,
      message: `Uploading ${file.name}...`
    });

    try {
      const response = await uploadFile(formData);
      setCurrentFile(response.filename);
      setUploadStatus({
        success: true,
        message: `${response.filename} uploaded successfully`
      });
    } catch (error) {
      setUploadStatus({
        success: false,
        message: `Error: ${error.message}`
      });
    }
  };

  const handleSendQuestion = async () => {
    if (!questionText.trim() || !currentFile || isProcessing) return;

    setCurrentQuestion(questionText);
    setAnswer(null);
    setProvenanceSources([]);
    setIsProcessing(true);

    try {
      const response = await askQuestion(questionText, currentFile);
      setCurrentQuestionId(response.question_id);
      startPolling(response.question_id);
    } catch (error) {
      setAnswer(`Error: ${error.message}`);
      setIsProcessing(false);
    }
  };

  const startPolling = (questionId) => {
    // Clear any existing polling
    if (pollingInterval) clearInterval(pollingInterval);

    // Set up new polling
    const interval = setInterval(async () => {
      try {
        const progress = await checkProgress(questionId);
        
        if (progress.done) {
          clearInterval(interval);
          setIsProcessing(false);
        }
      } catch (error) {
        console.error('Error checking progress:', error);
      }
    }, 1000);

    setPollingInterval(interval);
  };

  const checkProgress = async (questionId) => {
    try {
      const data = await apiCheckProgress(questionId);
      
      if (data.progress > 0) {
        // We have some data, update the UI
        updateProvenanceSources(questionId, data.data);
        
        // If we have an answer, show it
        await getResults(questionId);
      }
      
      return data;
    } catch (error) {
      console.error('Error checking progress:', error);
      return { progress: 0, done: false };
    }
  };

  const getResults = async (questionId) => {
    try {
      const data = await apiGetResults(questionId);
      
      if (data.success) {
        if (data.answer) {
          setAnswer(data.answer);
        }
        
        if (data.provenance && data.provenance.length > 0) {
          updateProvenanceSources(questionId, data.provenance);
        }
      }
    } catch (error) {
      console.error('Error getting results:', error);
    }
  };

  const updateProvenanceSources = async (questionId, provenance) => {
    // Update sources with new content
    const enhancedProvenance = await Promise.all(
      provenance.map(async (source) => {
        if (!source.content && source.sentences_ids) {
          try {
            const sentenceData = await fetchSentences(questionId, source.sentences_ids);
            return {
              ...source,
              content: Object.values(sentenceData.sentences)
            };
          } catch (error) {
            console.error('Error fetching sentences:', error);
            return source;
          }
        }
        return source;
      })
    );
    
    setProvenanceSources(enhancedProvenance);
  };

  return (
    <div className="input-area">
      <label htmlFor="file-upload" className="upload-btn">
        <FontAwesomeIcon icon={faUpload} />
      </label>
      <input
        ref={fileInputRef}
        id="file-upload"
        type="file"
        accept=".pdf"
        style={{ display: 'none' }}
        onChange={handleFileUpload}
      />
      
      <div className="input-container">
        <input
          className="question-input"
          type="text"
          placeholder="When was this paper published?"
          value={questionText}
          onChange={(e) => setQuestionText(e.target.value)}
          onKeyPress={(e) => e.key === 'Enter' && handleSendQuestion()}
          disabled={!currentFile || isProcessing}
        />
        <button
          className="send-btn"
          onClick={handleSendQuestion}
          disabled={!currentFile || !questionText.trim() || isProcessing}
        >
          <FontAwesomeIcon icon={faArrowUp} />
        </button>
      </div>
    </div>
  );
};

export default InputArea; 